.sub-header{
    background: #650300;
}
.sub-menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.sub-menu ul{
    display: flex;
}
.sub-menu ul li a{
    color:white;
    font-size: 20px;
    display: block;
    padding: 13px 24px;
}
.sub-menu ul li a:hover{
    color:#650300;
    background: white;
}
.search{
    color:white;
    font-size: 29px;
}
.search:hover{
    opacity: 0.9;
}
@media (max-width:768px){
    .sub-header{
        display: none;
    }
}


.content{
    padding-top: 96px;
    padding-bottom: 80px;
}
.content h2{
    font-size: 48px;
    font-weight: bold;
    border-left: 8px solid #9F1213;
    padding-left: 16px;
    margin-bottom: 32px;
}
.blog-card{
    display: flex;
    margin-bottom: 60px;
}
.blog-card:last-child{
    margin-bottom: 0px;
}
.blog-card img{
    max-width: 50%;
    margin-right: 24px;
}
.blog-content{
    width: 49%;
    display: flex;
    flex-direction: column;
}
.blog-content h3{
    color:#650300;
    font-size: 32px;
}
.sub-title{
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    margin-bottom: 16px;
}
.date{
    opacity: 0.7;
}
.blog-content p{
    font-size: 20px;
    color:#555555;
}
.MORE{
    color:#650300;
    font-size: 36px;
    display: flex;
    margin-top: auto;
    justify-content: end;

}
.MORE:hover{
opacity: 0.9;
}

@media (max-width:991px){
    .blog-content h3{
        font-size: 24px;
    }
    .sub-title{ 
        font-size: 16px;
        margin-bottom: 4px;
    }
    .blog-content p{
        font-size: 16px;
    }
    .MORE{
        font-size: 16px;
        color:#555555;
        margin-top: -24px;
    }
}
@media (max-width:768px){
    .blog-card{
        display: block;
        width: 100%;
        flex-direction: column;
    }
    .blog-card p,.MORE{
        display: none;
    }
    .blog-card{
        margin-bottom: 24px;
    }

    .blog-content{
        padding-top: 8px;
    }
}





.page-box{
    display: flex;
    justify-content: center;
    padding-top: 124px;
}
.page{
    display: flex;
    border: 1px solid black;
}
.page a{
    font-size: 19px;
    padding: 12px 16px;
    display: block;
    color:black
}
.page a:hover{
    background: black;
    color:white
}
.boder-right{
    border-right: 1px solid black;
}
@media (max-width:991px){
    .page-box{
        padding-top: 80px;
    }
}
@media (max-width:768px){
    .page a{
        font-size: 16px;
        padding: 12px 14px;
    }
}